.app {
  display: flex;
  height: 100vh;

  .routes {
    width: 100%;
    background-color: #f0f0f0;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;

    .main_content {
      flex: 1 0 auto;
    }

    .footer_content {
      flex-shrink: 0;
    }

    a {
      color: unset !important;
    }
  }
}


